
.formContainer {
    width: 300px;
    padding: 20px;
    background-color: rgba(242, 242, 242, 0.8);
    border-radius: 5px;
    font-family: 'Lato', sans-serif;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
.formContainer__title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  .form {
    margin-bottom: 20px;
  }

  .form__label {
    display: block;
    margin-bottom: 5px;
    color: #666;
  }
  
  .form__input,
  .form__select {
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #ccc;
    font-size: 14px;
    margin-bottom: 10px;
    box-sizing: border-box;
  }

  .form__select {
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #ccc;
    font-size: 14px;
    margin-bottom: 10px;
    box-sizing: border-box;
  }

.form__label {
  display: block;
  margin-bottom: 5px;
  color: #666;
}


.result {
    text-align: center;
    font-weight: bold;
    color: #333;
    font-size: 16px;
    margin-top: 20px;
  }
  
  .form__button {
    width: 100%;
    padding: 10px;
    background-color: #4caf50;
    border: none;
    color: white;
    font-weight: bold;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 10px;
  }
  
  .form__button:hover {
    background-color: #45a049;
  }

  
